nav#menu {
  position: fixed;
  top: 50%;
  right: 30px;
  text-align: center;
  z-index: 10;
  transform: translate(-50%, -50%);
}
ul {
  list-style: none;
}
#menu {
  button {
    padding: 20px 2px;
    font-size: 20px;
    font-weight: 900;
    cursor: pointer;
    width: max-content;
    transition: 0.25s ease;
    outline: none;
    margin-bottom: 10px;
    &:hover {
      border-radius: 25px;
      background: #ff0054;
    }
  }
}
.menu--active {
  background: #ff0054;
}
@media (max-width: 767px) {
  #menu {
    display: none;
  }
  .nav#menu {
    right: 20px;
  }
}

.Container {
  margin-top: -46px;
}
@media (max-width: 767px) {
  .Container {
    margin-top: 0;
  }
}
